@use '../../styles/variables' as var;

.container{
  width: 100%;
  scroll-margin: 2rem;

  .committeeTitle {
    font-family: var.$primary-font-medium;
    font-size: 1.25rem;
    margin-bottom: 1.25rem;
  }
  
  .committeeContainer{
      margin-bottom: 2rem;
      @media only screen and (max-width: var.$medium-size-devices) {
        margin-bottom: 1.8rem;
      }
      @media only screen and (max-width: var.$small-size-devices) {
        margin-bottom: 1.6rem;
      }
  }
}

